import React, { Component, Fragment } from "react";
import { TweenMax, TimelineMax, EasePack, TextPlugin} from 'gsap';
import Head from "next/head";
import Page from "../layouts/main";
import Title from "../components/Title/Title";
import Footer from "../components/Footer/Footer";
import settings from "../util/settings";
import ReactGA from 'react-ga';
import data from '../static/json/data.json';

class Product extends Component {

  constructor(props) {
    super(props);
    this.state = {
      router: {},
      product: {}
    }
    this.ASSET_PATH = settings.ASSET_PATH + "images/"
  }

  componentDidMount() {
    var router = this.context.router,
        state = {};
    if (!settings.isServer()) {
      var urlChops = window.location.pathname.split("/"),
          lastChop = "", validURL = false
      for (var i = urlChops.length - 1; i > 0; i--) {
        if (urlChops[i] && urlChops[i] != "") {
          lastChop = urlChops[i]
          break
        }
      }
      for (var i = 0; i < data.products.length; i++) {
        if (data.products[i].url == lastChop) {
          state.product = data.products[i]
          validURL = true
        }
      }
      if (!validURL) router.push("/")
    }
    this.setState(state);
  }

  componentWillUnmount() {
  }

  render() {
    const { product } = this.state
    return (
      <Page id="Product" menu={{solidStyle: true}} ref={el => this.Page = el}>
        <div className="ContentContainer">
          <div className="row" style={{ alignItems: 'center' }}>
            <div className="col-md-6">
              <Title title={product.name || ""} altTheme />
              <div className="prod-img-container" ref={el => this.ProdImage = el}>
                <img src={this.ASSET_PATH + product.image} alt={product.name || ""} />
              </div>
            </div>
            <div className="col-md-6 subproduct-container">
            { product && product.subproducts &&
              <ul>
                { product.subproducts.map((item, index) => {
                    return (
                      <li key={index} className="subproduct">{item}</li>
                    )
                  })
                }
              </ul>
            }
            </div>
          </div>
        </div>
        <Footer />
      </Page>
    )
  }
}

export default Product



